import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';

const { Option } = Select;

@Form.create({ name: 'TeacherExam' })
class TeacherExam extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={24}>
            <Form.Item label="Giám thị 1">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">Nguyễn Văn A</Option>
                  <Option value="2">Trần Tuấn B</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={24}>
            <Form.Item label="Giám thị 2">
              {getFieldDecorator('system2', {})(
                <Select>
                  <Option value="1">Nguyễn Văn A</Option>
                  <Option value="2">Trần Tuấn B</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={24}>
            <Form.Item label="Giám thị 3">
              {getFieldDecorator('system3', {})(
                <Select>
                  <Option value="1">Nguyễn Văn A</Option>
                  <Option value="2">Trần Tuấn B</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default TeacherExam;



