import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Table, Popconfirm, Col, Modal } from 'antd';
import BtnEdit from 'components/common/button/BtnEdit';
import { BtnDelete, Btn } from 'components/common/button';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { dataExamOrganization } from 'dump/data';
import FromSearch from './FormSearch';

import TeacherExam from './TeacherExam';

const urlForm = '#exam-organization/form';

class ExamOrganization extends Component {
  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  render() {
    const columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'kh',
        key: 'id',
      },
      {
        title: 'Tên môn',
        dataIndex: 'tm',
        key: 'id',
      },
      {
        title: 'Đợt thi',
        dataIndex: 'dt',
        key: 'id',
      },
      {
        title: 'Lần thi',
        dataIndex: 'lt',
        key: 'id',
      },
      {
        title: 'Ngày thi',
        dataIndex: 'nt',
        key: 'id',
      },
      {
        title: 'Ca thi',
        dataIndex: 'ct',
        key: 'id',
      },
      {
        title: 'Giờ thi',
        dataIndex: 'gt',
        key: 'id',
      },
      {
        title: 'Số SV',
        dataIndex: 'ssv',
        key: 'id',
      },
      {
        title: 'Phòng thi',
        dataIndex: 'pt',
        key: 'id',
      },
      {
        title: 'Giám thị 1',
        dataIndex: 'gt1',
        key: 'id',
      },
      {
        title: 'Giám thị 2',
        dataIndex: 'gt2',
        key: 'id',
      },
      {
        title: 'Giám thị 3',
        dataIndex: 'gt3',
        key: 'id',
      },
      {
        title: 'Ghi chú',
        dataIndex: 'gc',
        key: 'id',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            <BtnEdit href={`${urlForm}/edit/${record.code}`} />
            <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
              <BtnDelete />
            </Popconfirm>
            <Btn style={{ marginTop: 5 }} icon="user-o" title="Phân GT" onClick={this.showModal} />
          </Fragment>
        ),
      },
    ];
    return (
      <Card title="Tổ chức thi">
        <Row>
          <Col span={24}>
            <BtnAddNew href={`${urlForm}/new`} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FromSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={dataExamOrganization} columns={columns} />
          </Col>
        </Row>
        <Modal
          title="Phân giám thị"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          style={{ top: 20 }}
        >
          <TeacherExam />
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ExamOrganization />
  </Page>
);



